import React, { useState, useEffect } from 'react';
import FilmCard from './film_card';
import LoadMoreButton from './load_more_button';
import Grid from '@material-ui/core/Grid';



function FilmsList() {
  const [filmList, setFilmList] = useState(null);
  const [loadList, setloadList] = useState(true);
  const [page, setPage] = useState(1);

  useEffect(() => {
    loadList && fetch('https://api.themoviedb.org/3/discover/movie?api_key=f46825213b5aa8bff450cdca03e35c8d&sort_by=popularity.desc&page=1')
      .then(b => b.json())
      .then(b => {
        setFilmList(b.results);
        setloadList(false);
      });
  });

  function loadMore() {
    setPage(page + 1);
    fetch('https://api.themoviedb.org/3/discover/movie?api_key=f46825213b5aa8bff450cdca03e35c8d&sort_by=popularity.desc&page=' + page)
      .then(b => b.json())
      .then(b => {
        setFilmList(filmList.concat(b.results));
      });
  }

  return (
    <Grid
      container
      direction="column"
      justify="center"
      alignItems="center">
      <Grid
        container
        direction="row"
        justify="center"
        alignItems="flex-start"
        spacing={4}>
        {filmList && filmList.map(value => (
          <Grid key={value.id} item>
            <FilmCard film={value} />
          </Grid>
        ))}
      </Grid>
      <LoadMoreButton onClick={loadMore} />
    </Grid>
  );
}

export default FilmsList;